import { Component, OnInit } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { AppState, getAuthUser } from '../store/reducers';
import { BaseComponent } from '../shared/helpers/base.component';
import { IUser } from '../models';
import { filter } from 'rxjs/operators';

@Component({
  selector: 'upb-rewards',
  templateUrl: './rewards.component.html',
  styleUrls: ['./rewards.component.scss']
})
export class RewardsComponent extends BaseComponent implements OnInit {
  public totalReward: number;
  constructor(private store: Store<AppState>) {
    super();
  }

  ngOnInit(): void {
    this.subs = this.store
      .pipe(
        select(getAuthUser),
        filter(Boolean)
      )
      .subscribe((user: IUser) => (this.totalReward = user.rewardsBalance));
  }
}
