import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { combineLatest } from 'rxjs';
import { select, Store } from '@ngrx/store';
import { filter, finalize } from 'rxjs/operators';

import { Brand, OrderData } from '../../types/brand';
import { AppState, getAuthUser, getCompany, getRewardsBrands, getRewardsError } from '../../store/reducers';
import { BaseComponent } from '../../shared/helpers/base.component';
import { GetBrandsCatalog } from '../../store/actions/rewards';
import { RewardsService } from '../../api/rewards.service';
import { Company } from '../../types/company';
import { IUser } from '../../models';
import { SetUser } from '../../store/actions/auth';
import { environment } from '../../../environments/environment';
import { debounce } from 'lodash-es';

@Component({
  selector: 'upb-brand-details',
  templateUrl: './brand-details.component.html',
  styleUrls: ['./brand-details.component.scss']
})
export class BrandDetailsComponent extends BaseComponent implements OnInit {
  public totalReward: number;
  public brandData: Brand;
  public maxReward: number;
  public minReward: number;
  public rewardError: boolean;
  public rewardSuccess: boolean;
  public toast: any;
  public fixedValues: boolean;
  public rewardValue: number;
  public canOrder: boolean;
  public loading: boolean;
  private selectedCompany: Company;
  private user: IUser;

  constructor(
    private route: ActivatedRoute,
    protected router: Router,
    protected store: Store<AppState>,
    private rewardsService: RewardsService
  ) {
    super();
  }

  ngOnInit(): void {
    this.subs = combineLatest(this.route.params).subscribe(params => this.getInitData(params[0].brandKey));
    this.subs = this.store
      .pipe(
        select(getRewardsError),
        filter(Boolean)
      )
      .subscribe(error => {
        this.toast = {
          message: error.error.errorMessage,
          type: 'danger'
        };
        this.rewardError = true;
        this.brandData = null;
      });
  }

  public goBack(): void {
    this.router.navigate(['..'], { relativeTo: this.route, queryParamsHandling: 'merge' });
  }

  public validateRewardAmount(): void {
    debounce(this.isValidRewardAmount(), 500);
  }

  public isValidRewardAmount(): Boolean | any {
    this.rewardValue = Number(this.rewardValue);
    if (this.fixedValues || (this.minReward <= this.rewardValue && this.rewardValue <= this.maxReward)) {
      this.toast = null;
      this.rewardError = false;
      return true;
    }
    const minMax = this.rewardValue > this.maxReward ? 'maximum' : 'minimum';
    const amount = this.rewardValue > this.maxReward ? this.maxReward : this.minReward;
    this.toast = {
      message: `The ${minMax} reward for this card is $${amount}`,
      type: 'danger'
    };
    this.rewardError = true;
    return false;
  }

  public confirmReward(): void {
    if (!this.isValidRewardAmount()) {
      const amount = this.rewardValue > this.maxReward ? this.maxReward : this.minReward;
      this.rewardValue = +amount;
      this.validateRewardAmount();
      return;
    }
    // prettier-ignore
    // tslint:disable-next-line:max-line-length
    const successMessage = `Your rewards redemption was successful. Most rewards card orders are delivered via email within ${environment.REWARD_EMAIL_DELIVERY_MINUTES} minutes, but please allow up to 24 hours.`;
    this.loading = true;
    const data: OrderData = {
      amount: this.rewardValue,
      companyId: this.selectedCompany.id,
      utid: this.brandData.items[0].utid
    };
    if (this.fixedValues) {
      const fixedItem = this.brandData.items.find(item => item.faceValue === this.rewardValue);
      data.utid = fixedItem.utid;
    }
    this.user.rewardsBalance = this.user.rewardsBalance - this.rewardValue;
    this.store.dispatch(new SetUser(this.user));
    this.subs = this.rewardsService
      .orderReward(this.user.id, data)
      .pipe(finalize(() => (this.loading = false)))
      .subscribe(
        result => this.onSuccessOrder(result, data, successMessage),
        () => {
          this.rewardSuccess = false;
          this.toast = {
            message: 'Service Unavailable! Please reload page and try again.',
            type: 'danger'
          };
          this.rewardError = true;
          this.getInitData(this.brandData.brandKey);
        }
      );
  }

  protected onSuccessOrder(result, data: OrderData, successMessage: string) {
    this.user.rewardsBalance = Math.min(this.user.rewardsBalance, result.balance);
    this.store.dispatch(new SetUser(this.user));
    this.getInitData(this.brandData.brandKey);
    this.rewardSuccess = true;
    this.toast = {
      message: successMessage,
      type: 'success'
    };
  }

  public toastClosed(event: boolean): void {
    this.rewardError = event;
    this.rewardSuccess = event;
  }

  public getInitData(brandKey: string): void {
    this.subs = combineLatest(
      this.store.pipe(select(getAuthUser)),
      this.store.pipe(select(getCompany)),
      this.store.pipe(select(getRewardsBrands))
    ).subscribe(([user, company, brands]) => {
      if (!brands.length) {
        this.store.dispatch(new GetBrandsCatalog());
        return;
      }
      this.totalReward = user.rewardsBalance;
      this.selectedCompany = company.selectedCompany;
      this.user = user;
      this.brandData = brands.find(brand => brand.brandKey === brandKey);
      if (this.brandData.items.length > 1 || this.brandData.items[0].valueType === 'FIXED_VALUE') {
        this.fixedValues = true;
        this.rewardValue = this.brandData.items[0].faceValue;
        this.canOrder = this.totalReward >= this.rewardValue;
        return;
      }

      if (this.totalReward < this.brandData.items[0].maxValue) {
        this.maxReward = Math.floor(this.totalReward);
      } else {
        this.maxReward = this.brandData.items[0].maxValue;
      }
      this.minReward = Math.ceil(this.brandData.items[0].minValue);
      this.rewardValue = this.minReward;

      this.canOrder = this.totalReward >= this.minReward;
    });
  }
}
