import { Component, OnInit } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { filter } from 'rxjs/operators';
import { combineLatest } from 'rxjs';
import { Brand } from '../../types/brand';
import { BaseComponent } from '../../shared/helpers/base.component';
import { AppState, getCompany, getRewardsBrands, getRewardsError } from '../../store/reducers';
import { GetBrandsCatalog } from '../../store/actions/rewards';

@Component({
  selector: 'upb-brands-list',
  templateUrl: './brands-list.component.html',
  styleUrls: ['./brands-list.component.scss']
})
export class BrandsListComponent extends BaseComponent implements OnInit {
  public brandsList: Brand[];
  public sortedBrands: Brand[];
  public error: string;
  public sortModel: 'CA' | 'US';
  public isCAD: boolean;

  constructor(private store: Store<AppState>) {
    super();
  }

  ngOnInit(): void {
    this.subs = combineLatest(this.store.pipe(select(getRewardsBrands)), this.store.pipe(select(getCompany)))
      .pipe(filter(([brands, company]) => Boolean(brands && company)))
      .subscribe(([brands, company]) => {
        if (!brands.length) {
          this.store.dispatch(new GetBrandsCatalog());
          return;
        }
        this.brandsList = brands;
        this.isCAD = company.selectedCompany.currency === 'cad';
        this.sortModel = this.isCAD ? 'CA' : 'US';
        this.doSort();
        this.error = '';
      });

    this.subs = this.store
      .pipe(
        select(getRewardsError),
        filter(Boolean)
      )
      .subscribe(error => (this.error = error.error.errorMessage));
  }

  private doSort(): void {
    if (!this.brandsList) {
      return;
    }
    this.sortedBrands = this.brandsList.filter(brand => {
      // Remove Visa USA card from Canadian Rewards
      if (this.sortModel === 'CA' && brand.brandKey === 'B238587') {
        return false;
      }
      return brand.items[0].countries.includes(this.sortModel);
    });
  }
}
